/* src/index.css */

body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    color: #333;
   background-image: linear-gradient(rgb(7, 238, 255),rgb(0, 183, 255),rgb(104, 252, 111));
border-radius: 16px;
box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
backdrop-filter: blur(5px);
-webkit-backdrop-filter: blur(5px);
border: 1px solid rgba(255, 255, 255, 0.3);

  }




  /* src/index.css */

nav ul {
    list-style: none;
    padding: 0;
    display: flex;
    gap: 1rem;
    text-align: center;
  }
  
  nav ul li {
    display: inline;
    
  }

  

img {
  border-radius: 5px;
  margin-top: 1rem;
}

  
  nav ul li a {
    color: black;
    text-decoration: none;
    
  }
  
  nav ul li a:hover {
    text-decoration: underline;
  }
  
  
  header {
    
    color:black;
    padding: 1rem;
    text-align: center;
    border-radius: 0px 0px 15px 15px;
    background: rgba(255, 255, 255, 0.6);
    
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(2.1px);
    -webkit-backdrop-filter: blur(2.1px);
    border: 1px solid rgba(255, 255, 255, 1);
  
  }
  
  main {
    padding: 2rem;
  }
  
  h2 {
    margin-top: 0;
  }
  
  label {
    display: block;
    margin-bottom: 1rem;
  }
  
  input {
    margin-left: 0.5rem;
    padding: 0.5rem;
    border-radius: 5px;
    border: 1px solid #ddd;
  }
  
  button {
    background-color:red;
    color: white;
    border: none;
    padding: 0.5rem 1rem;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #45a049;
  }
  
  h3 {
    margin-bottom: 0.5rem;
  }
  .container{
    /* From https://css.glass */
background: rgba(255, 255, 255, 0.6);
border-radius: 16px;
padding: 20px;
box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
backdrop-filter: blur(2.1px);
-webkit-backdrop-filter: blur(2.1px);
border: 1px solid rgba(255, 255, 255, 1);
  }